const express = require('express'),
    app = express(),
    mongoose = require('mongoose'),
    bodyParser = require('body-parser'),
    routes = require('./api/routes'),
    port = process.env.PORT || 8000;



mongoose.Promise = global.Promise;
mongoose.connect('mongodb://localhost/Blogdb');

// parse application/x-www-form-urlencoded
app.use(bodyParser.urlencoded({ extended: false }));

// parse application/json
app.use(bodyParser.json({limit:'10Mb'}));

//controllers
const PostController = require('./api/controllers/posts');
const CommentController = require('./api/controllers/comments');

PostController(app);
// CommentController(app);

routes(app);


app.use( (req, res, next) => {
    res.header("Access-Control-Allow-Origin", "*");
    res.header("Access-Control-Allow-Headers",  `Authorization,Origin, X-Requested-With,  Content-Type, Accept, Access-Control-Allow-Origin, Access-Control-Allow-Headers,  Access-Control-Allow-Methods`);
    next();
});



app.get('/api', (req, res, next) => {
    res.send('API is running');
});

app.listen(port, () => console.log(`API server started on: http://localhost:${port}/api`));



